AA,CACA,eAAA,CACA,gBAAA,CACA,oBAAA,CACA,gBAAA,CACA,mBAAA,CACA,kBAAA,CACA,iBAAA,CACA,mBAAA,CACA,gBAAA,CACA,cAAA,CACA,SAAA,CACA,cAAA,CAEA,eAAA,CACA,cAAA,CACA,sDAAA,CACA,8DAGI,kBAAA,CACA,SAAA,CACA,uBAAA,CAEJ,uDAEI,QAAA,CACA,iBAAA,CAEJ,8CAEI,cAAA,CACA,cAAA,CAEJ,4CAEI,eAAA,CACA,cAAA,CACA,0EACI,OAAA,CACA,MAAA,CACA,gBAAA,CACA,6BAAA,CACA,+CAAA,CAGR,0CAEI,gBAAA,CACA,aAAA,CACA,wEACI,OAAA,CACA,OAAA,CACA,gBAAA,CACA,6BAAA,CACA,8CAAA,CAQJ,kPACI,QAAA,CACA,wBAAA,CACA,6CAAA,CAGR,iCACI,UAAA,CACA,mBAAA,CAEJ,kCACI,SAAA,CACA,mBAAA,CAOA,0QACI,KAAA,CACA,wBAAA,CACA,gDAAA,CAGR,4EAEI,QAAA,CACA,iBAAA,CAEJ,oCACI,UAAA,CACA,gBAAA,CAEJ,qCACI,SAAA,CACA,gBAAA,CAIR,eACI,eAAA,CACA,eAAA,CACA,6CAAA,CACA,4BAAA,CACA,iBAAA,CACA,kCAAA,CAGJ,+BACI,iBAAA,CACA,OAAA,CACA,QAAA,CACA,wBAAA,CACA,kBAAA,CCzIJ,gBACC,eAAA,CACA,eAAA,CACA,6CAAA,CACA,4BAAA,CACA,4CAAA,CACA,YAAA,CACA,kBAAA,CACA,eAAA,CACA,cAAA,CACA,YAAA,CACA,kCAAA,CAEA,6BACC,iBAAA,CACA,KAAA,CACA,OAAA,CACA,UAAA,CACA,UAAA,CACA,YAAA,CAEA,0BAAA,CACA,2BAAA,CACA,gBAAA,CACA,kBAAA,CACA,eAAA,CAEA,0GACC,cAAA,CACA,SAAA,CAIH,uBACC,UAAA,CAGD,aACC,wCAAA,CAED,YACC,0CAAA,CAED,eACC,0CAAA,CAED,eACC,0CAAA,CC1CC,oDACC,+BAAA,CAIA,0DACC,eAAA,CAED,2EACC,4BAAA,CACA,wBAAA,CAEA,oPAGC,SAAA,CAID,mFACC,YAAA,CAED,sFACC,YAAA,CAED,mGACC,WAAA,CAMJ,sBAEC,4BAAA,CAKD,kCACC,aAAA,CAID,0BACC,oBAAA,CAID,oBACC,gBAAA,CACA,kCAAA,CACA,qBAAA,CACA,oBAAA,CACA,gBAAA,CAED,+KAIC,iBAAA,CAID,oBACC,iBAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,WA1Ec,CA2Ed,qBAAA,CACA,sBACC,iBAAA,CACA,+BAAA,CACA,wBACC,+BAAA,CACA,gBAAA,CACA,kBAAA,CAEA,YAAA,CACA,YAAA,CACA,gBAAA","sourcesContent":["// CSS4 Variables\n// Remember, you cannot use scss functions with css4 variables\n// All css4 variables must be fixed! Scss is a PRE processor\n// css4 variables are processed after scss!\n:root {\n\t--color-main-text: #{$color-main-text};\n\t--color-main-background: #{$color-main-background};\n\t--color-main-background-translucent: #{$color-main-background-translucent};\n\n\t// To use like this: background-image: linear-gradient(0, var(--gradient-main-background));\n\t--gradient-main-background: var(--color-main-background) 0%, var(--color-main-background-translucent) 85%, transparent 100%;\n\n\t--color-background-hover: #{$color-background-hover};\n\t--color-background-dark: #{$color-background-dark};\n\t--color-background-darker: #{$color-background-darker};\n\n\t--color-placeholder-light: #{$color-placeholder-light};\n\t--color-placeholder-dark: #{$color-placeholder-dark};\n\n\t--color-primary: #{$color-primary};\n\t--color-primary-hover: #{$color-primary-hover};\n\t--color-primary-light: #{$color-primary-light};\n\t--color-primary-light-hover: #{$color-primary-light-hover};\n\t--color-primary-text: #{$color-primary-text};\n\t--color-primary-light-text: #{$color-primary-light-text};\n\t--color-primary-text-dark: #{$color-primary-text-dark};\n\t--color-primary-element: #{$color-primary-element};\n\t--color-primary-element-hover: #{$color-primary-element-hover};\n\t--color-primary-element-light: #{$color-primary-element-light};\n\t--color-primary-element-lighter: #{$color-primary-element-lighter};\n\n\t--color-error: #{$color-error};\n\t--color-error-hover: #{$color-error-hover};\n\t--color-warning: #{$color-warning};\n\t--color-warning-hover: #{$color-warning-hover};\n\t--color-success: #{$color-success};\n\t--color-success-hover: #{$color-success-hover};\n\n\t--color-text-maxcontrast: #{$color-text-maxcontrast};\n\t--color-text-light: #{$color-main-text};\n\t--color-text-lighter: #{$color-text-maxcontrast};\n\n\t--image-logo: #{$image-logo};\n\t--image-login-background: #{$image-login-background};\n\t--image-logoheader: #{$image-logoheader};\n\t--image-favicon: #{$image-favicon};\n\n\t--color-loading-light: #{$color-loading-light};\n\t--color-loading-dark: #{$color-loading-dark};\n\n\t--color-box-shadow: #{$color-box-shadow};\n\n\t--color-border: #{$color-border};\n\t--color-border-dark: #{$color-border-dark};\n\n\n\t--border-radius: #{$border-radius};\n\t--border-radius-large: #{$border-radius-large};\n\t--border-radius-pill: #{$border-radius-pill};\n\n\t--font-face: #{$font-face};\n\t--default-font-size: #{$default-font-size};\n\n\t--default-line-height: #{$default-line-height};\n\n\t--animation-quick: #{$animation-quick};\n\t--animation-slow: #{$animation-slow};\n\n\t--header-height: #{$header-height};\n\n\t--breakpoint-mobile: #{$breakpoint-mobile};\n}\n","/**\n * @copyright Copyright (c) 2016, John Molakvoæ <skjnldsv@protonmail.com>\n * @copyright Copyright (c) 2016, Lukas Reschke <lukas@statuscode.ch>\n * @copyright Copyright (c) 2016, Robin Appelman <robin@icewind.nl>\n * @copyright Copyright (c) 2016, Julius Haertl <jus@bitgrid.net>\n * @copyright Copyright (c) 2016, Joas Schilling <coding@schilljs.com>\n * @copyright Copyright (c) 2016, Morris Jobke <hey@morrisjobke.de>\n * @copyright Copyright (c) 2016, Christoph Wurst <christoph@winzerhof-wurst.at>\n * @copyright Copyright (c) 2016, Raghu Nayyar <hey@raghunayyar.com>\n * @copyright Copyright (c) 2011-2017, Jan-Christoph Borchardt <hey@jancborchardt.net>\n * @copyright Copyright (c) 2019-2020, Gary Kim <gary@garykim.dev>\n *\n * @license GNU AGPL version 3 or any later version\n *\n */\n\nhtml, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, code, del, dfn, em, img, q, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, dialog, figure, footer, header, hgroup, nav, section {\n\tmargin: 0;\n\tpadding: 0;\n\tborder: 0;\n\toutline: 0;\n\tfont-weight: inherit;\n\tfont-size: 100%;\n\tfont-family: inherit;\n\tvertical-align: baseline;\n\tcursor: default;\n\tscrollbar-color: var(--color-border-dark) transparent;\n\tscrollbar-width: thin;\n}\n\nhtml, body {\n\theight: 100%;\n}\n\narticle, aside, dialog, figure, footer, header, hgroup, nav, section {\n\tdisplay: block;\n}\n\nbody {\n\tline-height: 1.5;\n}\n\ntable {\n\tborder-collapse: separate;\n\tborder-spacing: 0;\n\twhite-space: nowrap;\n}\n\ncaption, th, td {\n\ttext-align: left;\n\tfont-weight: normal;\n}\n\ntable, td, th {\n\tvertical-align: middle;\n}\n\na {\n\tborder: 0;\n\tcolor: var(--color-main-text);\n\ttext-decoration: none;\n\tcursor: pointer;\n\t* {\n\t\tcursor: pointer;\n\t}\n}\n\na.external {\n\tmargin: 0 3px;\n\ttext-decoration: underline;\n}\n\ninput {\n\tcursor: pointer;\n\t* {\n\t\tcursor: pointer;\n\t}\n}\n\nselect, .button span, label {\n\tcursor: pointer;\n}\n\nul {\n\tlist-style: none;\n}\n\nbody {\n\tbackground-color: var(--color-main-background);\n\tfont-weight: normal;\n\t/* bring the default font size up to 15px */\n\tfont-size: var(--default-font-size);\n\tline-height: var(--default-line-height);\n\tfont-family: var(--font-face);\n\tcolor: var(--color-main-text);\n}\n\n.two-factor-header {\n\ttext-align: center;\n}\n\n.two-factor-provider {\n\ttext-align: center;\n\twidth: 258px !important;\n\tdisplay: inline-block;\n\tmargin-bottom: 0 !important;\n\tbackground-color: var(--color-background-darker) !important;\n\tborder: none !important;\n}\n\n.two-factor-link {\n\tdisplay: inline-block;\n\tpadding: 12px;\n\tcolor: var(--color-text-lighter);\n}\n\n.float-spinner {\n\theight: 32px;\n\tdisplay: none;\n}\n\n#nojavascript {\n\tposition: fixed;\n\ttop: 0;\n\tbottom: 0;\n\tleft: 0;\n\theight: 100%;\n\twidth: 100%;\n\tz-index: 9000;\n\ttext-align: center;\n\tbackground-color: var(--color-background-darker);\n\tcolor: var(--color-primary-text);\n\tline-height: 125%;\n\tfont-size: 24px;\n\tdiv {\n\t\tdisplay: block;\n\t\tposition: relative;\n\t\twidth: 50%;\n\t\ttop: 35%;\n\t\tmargin: 0px auto;\n\t}\n\ta {\n\t\tcolor: var(--color-primary-text);\n\t\tborder-bottom: 2px dotted var(--color-main-background);\n\t\t&:hover, &:focus {\n\t\t\tcolor: var(--color-primary-text-dark);\n\t\t}\n\t}\n}\n\n/* SCROLLING */\n\n::-webkit-scrollbar {\n\twidth: 12px;\n\theight: 12px\n}\n\n::-webkit-scrollbar-track-piece {\n\tbackground-color: transparent;\n}\n\n::-webkit-scrollbar-thumb {\n\tbackground: var(--color-border-dark);\n\tborder-radius: var(--border-radius-large);\n\tborder: 2px solid transparent;\n\tbackground-clip: content-box;\n}\n\n\n/* SELECTION */\n\n::selection {\n\tbackground-color: var(--color-primary-element);\n\tcolor: var(--color-primary-text);\n}\n\n\n/* CONTENT ------------------------------------------------------------------ */\n\n#controls {\n\tbox-sizing: border-box;\n\tposition: 'sticky';\n\theight: 44px;\n\tpadding: 0;\n\tmargin: 0;\n\tbackground-color: var(--color-main-background-translucent);\n\tz-index: 62; /* must be above the filelist sticky header and texteditor menubar */\n\t-webkit-user-select: none;\n\t-moz-user-select: none;\n\t-ms-user-select: none;\n\tuser-select: none;\n\tdisplay: flex;\n\ttop: var(--header-height);\n}\n\n/* position controls for apps with app-navigation */\n\n.viewer-mode #app-navigation + #app-content #controls {\n\tleft: 0;\n}\n\n#app-navigation * {\n\tbox-sizing: border-box;\n}\n\n#controls .actions {\n\t> div,\n\t& {\n\t\t> .button, button {\n\t\t\tbox-sizing: border-box;\n\t\t\tdisplay: inline-block;\n\t\t\tdisplay: flex;\n\t\t\theight: 36px;\n\t\t\twidth: 36px;\n\t\t\tpadding: 9px; // width - border - icon width = 18px\n\t\t\talign-items: center;\n\t\t\tjustify-content: center;\n\t\t}\n\t\t.button.hidden {\n\t\t\tdisplay: none;\n\t\t}\n\t}\n}\n\n/* EMPTY CONTENT DISPLAY ------------------------------------------------------------ */\n\n#emptycontent,\n.emptycontent {\n\tcolor: var(--color-text-maxcontrast);\n\ttext-align: center;\n\tmargin-top: 30vh;\n\twidth: 100%;\n\t#app-sidebar & {\n\t\tmargin-top: 10vh;\n\t}\n\t.emptycontent-search {\n\t\tposition: static;\n\t}\n\th2 {\n\t\tmargin-bottom: 10px;\n\t}\n\t[class^='icon-'],\n\t[class*='icon-'] {\n\t\tbackground-size: 64px;\n\t\theight: 64px;\n\t\twidth: 64px;\n\t\tmargin: 0 auto 15px;\n\t\t&:not([class^='icon-loading']),\n\t\t&:not([class*='icon-loading']) {\n\t\t\topacity: .4;\n\t\t}\n\t}\n}\n\n/* LOG IN & INSTALLATION ------------------------------------------------------------ */\n\n#datadirContent label {\n\twidth: 100%;\n}\n\n/* strengthify wrapper */\n\n/* General new input field look */\n\n/* Nicely grouping input field sets */\n\n.grouptop, .groupmiddle, .groupbottom {\n\tposition: relative;\n\t-webkit-user-select: none;\n\t-moz-user-select: none;\n\t-ms-user-select: none;\n\tuser-select: none;\n}\n\n/* Show password toggle */\n\n#show, #dbpassword {\n\tposition: absolute;\n\tright: 1em;\n\ttop: .8em;\n\tfloat: right;\n}\n\n#show + label, #dbpassword + label {\n\tright: 21px;\n\ttop: 15px !important;\n\tmargin: -14px !important;\n\tpadding: 14px !important;\n}\n\n#show:checked + label, #dbpassword:checked + label, #personal-show:checked + label {\n\topacity: .8;\n}\n\n#show + label, #dbpassword + label, #personal-show + label {\n\tposition: absolute !important;\n\theight: 20px;\n\twidth: 24px;\n\tbackground-image: var(--icon-toggle-000);\n\tbackground-repeat: no-repeat;\n\tbackground-position: center;\n\topacity: .3;\n}\n\n/* Feedback for keyboard focus and mouse hover */\n#show,\n#dbpassword,\n#personal-show {\n\t&:focus + label {\n\t\topacity: 1;\n\t}\n\t+ label:hover {\n\t\topacity: 1;\n\t}\n}\n\n#show + label:before, #dbpassword + label:before, #personal-show + label:before {\n\tdisplay: none;\n}\n\n#pass2, input[name='personal-password-clone'] {\n\tpadding-right: 30px;\n}\n\n.personal-show-container {\n\tposition: relative;\n\tdisplay: inline-block;\n\tmargin-right: 6px;\n}\n#personal-show + label {\n\tdisplay: block;\n\tright: 0;\n\tmargin-top: -43px;\n\tmargin-right: -4px;\n\tpadding: 22px;\n}\n\n/* Warnings and errors are the same */\n\n#body-user .warning, #body-settings .warning {\n\tmargin-top: 8px;\n\tpadding: 5px;\n\tborder-radius: var(--border-radius);\n\tcolor: var(--color-primary-text);\n\tbackground-color: var(--color-warning);\n}\n\n.warning {\n\tlegend, a {\n\t\tcolor: var(--color-primary-text) !important;\n\t\tfont-weight: bold !important;\n\t}\n}\n\n.error:not(.toastify) 