verflow: hidden;\n\ttext-overflow: ellipsis;\n\t// Take full width to push the header-shared-by bellow (if any)\n\tflex: 1 1 100%;\n}\n\n.header-shared-by  {\n\tcolor: var(--color-primary-text);\n\tposition: relative;\n\tfont-weight: 300;\n\tfont-size: 11px;\n\tline-height: 11px;\n\toverflow: hidden;\n\ttext-overflow: ellipsis;\n}\n\n/* do not show menu toggle on public share links as there is no menu */\n#body-public #header .icon-caret {\n\tdisplay: none;\n}\n\n/* NAVIGATION --------------------------------------------------------------- */\nnav[role='navigation'] {\n\tdisplay: inline-block;\n\twidth: var(--header-height);\n\theight: var(--header-height);\n\tmargin-left: -var(--header-height);\n\tposition: relative;\n}\n\n#header .header-left > nav > #navigation {\n\tposition: relative;\n\tleft: 25px; /* half the togglemenu */\n\ttransform: translateX(-50%);\n\twidth: 160px;\n}\n\n#header .header-left > nav > #navigation,\n.ui-datepicker,\n.ui-timepicker.ui-widget {\n\tbackground-color: var(--color-main-background);\n\tfilter: drop-shadow(0 1px 10px var(--color-box-shadow));\n\t&:after {\n\t\t/* position of dropdown arrow */\n\t\tleft: 50%;\n\t\tbottom: 100%;\n\t\tborder: solid transparent;\n\t\tcontent: ' ';\n\t\theight: 0;\n\t\twidth: 0;\n\t\tposition: absolute;\n\t\tpointer-events: none;\n\t\tborder-color: rgba(0, 0, 0, 0);\n\t\tborder-bottom-color: var(--color-main-background);\n\t\tborder-width: 10px;\n\t\tmargin-left: -10px; /* border width */\n\t}\n}\n\n#navigation {\n\tbox-sizing: border-box;\n\t.in-header {\n\t\tdisplay: none;\n\t}\n}\n\n/* USER MENU -----------------------------------------------------------------*/\n#settings {\n\tdisplay: inline-block;\n\theight: 100%;\n\tcursor: pointer;\n\tflex: 0 0 auto;\n\n\t/* User menu on the right */\n\t#expand {\n\t\topacity: 1; /* override icon opacity */\n\t\tpadding-right: 12px;\n\n\t\t&:hover,\n\t\t&:focus,\n\t\t&:active {\n\t\t\tcolor: var(--color-primary-text);\n\n\t\t\t#expandDisplayName,\n\t\t\t.avatardiv{\n\t\t\t\tborder-radius: 50%;\n\t\t\t\tborder: 2px solid var(--color-primary-text);\n\t\t\t\tmargin: -2px;\n\t\t\t}\n\t\t\t.avatardiv{\n\t\t\t\tbackground-color: var(--color-primary-text);\n\t\t\t}\n\t\t\t#expandDisplayName {\n\t\t\t\topacity: 1;\n\t\t\t}\n\t\t}\n\n\t\t/* Profile picture in header */\n\t\t.avatardiv {\n\t\t\tcursor: pointer;\n\t\t\theight: 32px;\n\t\t\twidth: 32px;\n\n\t\t\timg {\n\t\t\t\topacity: 1;\n\t\t\t\tcursor: pointer;\n\t\t\t}\n\t\t\t/* do not show display name when profile picture is present */\n\t\t\t&.avatardiv-shown + #expandDisplayName {\n\t\t\t\tdisplay: none;\n\t\t\t}\n\t\t}\n\n\t\t#expandDisplayName {\n\t\t\tpadding: 8px;\n\t\t\topacity: .6;\n\t\t\tcursor: pointer;\n\n\t\t\t/* full opacity for gear icon if active */\n\t\t\t#body-settings & {\n\t\t\t\topacity: 1;\n\t\t\t}\n\t\t}\n\n\t\t/* show triangle below user menu if active */\n\t\t#body-settings &:before {\n\t\t\tcontent: ' ';\n\t\t\theight: 0;\n\t\t\twidth: 0;\n\t\t\tposition: absolute;\n\t\t\tpointer-events: none;\n\t\t\tborder: 0 solid transparent;\n\t\t\tborder-bottom-color: var(--color-main-background);\n\t\t\tborder-width: 10px;\n\t\t\tbottom: 0;\n\t\t\tz-index: 100;\n\t\t\tdisplay: block;\n\t\t}\n\t}\n\n\t#expanddiv:after {\n\t\tright: 22px;\n\t}\n}\n\n/* Apps menu */\n#appmenu {\n\tdisplay: inline-flex;\n\tmin-width: var(--header-height);\n\tz-index: 2;\n\n\tli {\n\t\tposition: relative;\n\t\tcursor: pointer;\n\t\tpadding: 0 2px;\n\t\tdisplay: flex;\n\t\tjustify-content: center;\n\n\t\ta {\n\t\t\tposition: relative;\n\t\t\tdisplay: flex;\n\t\t\tmargin: 0;\n\t\t\theight: var(--header-height);\n\t\t\twidth: var(--header-height);\n\t\t\talign-items: center;\n\t\t\tjustify-content: center;\n\t\t\topacity: .6;\n\t\t\t// Make sure most app names don’t ellipsize\n\t\t\tletter-spacing: -0.5px;\n\t\t\tfont-size: 12px;\n\t\t}\n\n\t\t/* focused app visual feedback */\n\t\t&:hover a,\n\t\ta:focus,\n\t\ta.active {\n\t\t\topacity: 1;\n\t\t\tfont-weight: bold;\n\t\t}\n\n\t\t// Text size back to normal for hover/focus\n\t\t&:hover a,\n\t\ta:focus {\n\t\t\tfont-size: 14px;\n\t\t}\n\n\t\t&:hover a + span,\n\t\ta:focus + span,\n\t\t&:hover span,\n\t\t&:focus span,\n\t\ta:focus span,\n\t\ta.active span {\n\t\t\tdisplay: inline-block;\n\t\t\ttext-overflow: initial;\n\t\t\twidth: auto;\n\t\t\toverflow: hidden;\n\t\t\tpadding: 0 5px;\n\t\t\tz-index: 2;\n\t\t}\n\n\t\t/* hidden apps menu */\n\t\timg,\n\t\t.icon-more-white {\n\t\t\tdisplay: inline-block;\n\t\t\twidth: 20px;\n\t\t\theight: 20px;\n\t\t}\n\n\t\t/* App title */\n\t\tspan {\n\t\t\topacity: 0;\n\t\t\tposition: absolute;\n\t\t\tcolor: var(--color-primary-text);\n\t\t\tbottom: 2px;\n\t\t\twidth: 100%;\n\t\t\ttext-align: center;\n\t\t\toverflow: hidden;\n\t\t\ttext-overflow: ellipsis;\n\t\t\ttransition: all var(--animation-quick) ease;\n\t\t\tpointer-events: none;\n\t\t}\n\n\t\t/* Set up transitions for showing app titles on hover */\n\t\t/* App icon */\n\t\tsvg,\n\t\t.icon-more-white {\n\t\t\ttransition: transform var(--animation-quick) ease;\n\t\t}\n\n\t\t/* Triangle */\n\t\ta::before {\n\t\t\ttransition: border var(--animation-quick) ease;\n\t\t}\n\t}\n\n\t/* Show all app titles on hovering app menu area */\n\t&:hover {\n\t\tli {\n\t\t\t/* Move up app icon */\n\t\t\tsvg,\n\t\t\t.icon-more,\n\t\t\t.icon-more-white,\n\t\t\t.icon-loading-small,\n\t\t\t.icon-loading-small-dark {\n\t\t\t\ttransform: translateY(-7px);\n\t\t\t}\n\n\t\t\t/* Show app title */\n\t\t\tspan {\n\t\t\t\topacity: .6;\n\t\t\t\tbottom: 2px;\n\t\t\t\tz-index: -1; /* fix clickability issue - otherwise we need to move the span into the link */\n\t\t\t}\n\n\t\t\t/* Prominent app title for current and hovered/focused app */\n\t\t\t&:hover span,\n\t\t\t&:focus span,\n\t\t\t.active + span {\n\t\t\t\topacity: 1;\n\t\t\t}\n\n\t\t\t/* Smaller triangle because of limited space */\n\t\t\ta::before {\n\t\t\t\tborder-width: 5px;\n\t\t\t}\n\t\t}\n\t}\n\n\t/* Also show app title on focusing single entry (showing all on focus is only possible with CSS4 and parent selectors) */\n\tli a:focus {\n\t\t/* Move up app icon */\n\t\tsvg,\n\t\t.icon-more,\n\t\t.icon-more-white,\n\t\t.icon-loading-small,\n\t\t.icon-loading-small-dark {\n\t\t\ttransform: translateY(-7px);\n\t\t}\n\n\t\t/* Show app title */\n\t\t& + span,\n\t\tspan {\n\t\t\topacity: 1;\n\t\t\tbottom: 2px;\n\t\t}\n\n\t\t/* Smaller triangle because of limited space */\n\t\t&::before {\n\t\t\tborder-width: 5px;\n\t\t}\n\t}\n\n\t/* show triangle below active app */\n\tli a::before {\n\t\tcontent: ' ';\n\t\theight: 0;\n\t\twidth: 0;\n\t\tposition: absolute;\n\t\tpointer-events: none;\n\t\tborder: 0 solid transparent;\n\t\tborder-bottom-color: var(--color-main-background);\n\t\tborder-width: 10px;\n\t\ttransform: translateX(-50%);\n\t\tleft: 50%;\n\t\tbottom: 0;\n\t\tdisplay: none;\n\t}\n\n\t/* triangle focus feedback */\n\tli a.active::before,\n\tli:hover a::before,\n\tli:hover a.active::before,\n\tli a:focus::before {\n\t\tdisplay: block;\n\t}\n\tli a.active::before {\n\t\tz-index: 99;\n\t}\n\tli:hover a::before,\n\tli a.active:hover::before,\n\tli a:focus::before {\n\t\tz-index: 101;\n\t}\n\n\tli.hidden {\n\t\tdisplay: none;\n\t}\n\n\t#more-apps {\n\t\tz-index: 3;\n\t}\n}\n\n.unread-counter {\n\tdisplay: none;\n}\n#apps .app-icon-notification,\n#appmenu .app-icon-notification {\n\tfill: var(--color-error);\n}\n\n#apps svg:not(.has-unread),\n#appmenu svg:not(.has-unread) {\n\t.app-icon-notification-mask {\n\t\tdisplay: none;\n\t}\n\t.app-icon-notification {\n\t\tdisplay: none;\n\t}\n}\n\n\n/* Skip navigation links – show only on keyboard focus */\n.skip-navigation {\n\tpadding: 11px;\n\tposition: absolute;\n\toverflow: hidden;\n\tz-index: 9999;\n\ttop: -999px;\n\tleft: 3px;\n\t/* Force primary color, otherwise too light focused color */\n\tbackground: var(--color-primary) !important;\n\n\t&.skip-content {\n\t\tleft: $navigation-width;\n\t\tmargin-left: 3px;\n\t}\n\n\t&:focus,\n\t&:active {\n\t\ttop: var(--header-height);\n\t}\n}\n\n/* SEARCHBOX ---------