\t\t-ms-transform: rotate(-90deg);\n\t\t\ttransform: rotate(-90deg);\n\t\t\tz-index: 105; // above a, under button\n\t\t\tbackground-color: var(--color-background-hover);\n\t\t\tborder-radius: 50%;\n\t\t\ttransition: opacity $animation-quick ease-in-out;\n\t\t}\n\n\t\t/* force padding on link no matter if 'a' has an icon class */\n\t\t> a:first-child {\n\t\t\tpadding-left: 44px;\n\t\t}\n\t\t&:hover,\n\t\t&:focus {\n\t\t\t&:before {\n\t\t\t\topacity: 1;\n\t\t\t}\n\t\t\t> .app-navigation-entry-bullet {\n\t\t\t\tbackground: transparent !important;\n\t\t\t}\n\t\t}\n\t\t&.open {\n\t\t\t&:before {\n\t\t\t\t-webkit-transform: rotate(0);\n\t\t\t\t-ms-transform: rotate(0);\n\t\t\t\ttransform: rotate(0);\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * App navigation utils, buttons and counters for drop down menu\n\t */\n\t.app-navigation-entry-utils {\n\t\tflex: 0 1 auto;\n\t\tul {\n\t\t\tdisplay: flex !important;\n\t\t\talign-items: center;\n\t\t\tjustify-content: flex-end;\n\t\t}\n\t\tli {\n\t\t\twidth: 44px !important;\n\t\t\theight: 44px;\n\t\t}\n\t\tbutton {\n\t\t\theight: 100%;\n\t\t\twidth: 100%;\n\t\t\tmargin: 0;\n\t\t\tbox-shadow: none;\n\t\t}\n\t\t.app-navigation-entry-utils-menu-button {\n\t\t\t/* Prevent bg img override if an icon class is set */\n\t\t\tbutton:not([class^='icon-']):not([class*=' icon-']) {\n\t\t\t\t@include icon-color('more', 'actions', $color-black, 1, true);\n\t\t\t}\n\t\t\t&:hover button,\n\t\t\t&:focus button {\n\t\t\t\tbackground-color: transparent;\n\t\t\t\topacity: 1;\n\t\t\t}\n\t\t}\n\t\t.app-navigation-entry-utils-counter {\n\t\t\toverflow: hidden;\n\t\t\ttext-align: right;\n\t\t\tfont-size: 9pt;\n\t\t\tline-height: 44px;\n\t\t\tpadding: 0 12px; /* Same padding as all li > a in the app-navigation */\n\n\t\t\t&.highlighted {\n\t\t\t\tpadding: 0;\n\t\t\t\ttext-align: center;\n\t\t\t\tspan {\n\t\t\t\t\tpadding: 2px 5px;\n\t\t\t\t\tborder-radius: 10px;\n\t\t\t\t\tbackground-color: var(--color-primary);\n\t\t\t\t\tcolor: var(--color-primary-text);\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * Editable entries\n\t */\n\t.app-navigation-entry-edit {\n\t\tpadding-left: 5px;\n\t\tpadding-right: 5px;\n\t\tdisplay: block;\n\t\twidth: calc(100% - 1px); /* Avoid border overlapping */\n\t\ttransition: opacity 250ms ease-in-out;\n\t\topacity: 0;\n\t\tposition: absolute;\n\t\tbackground-color: var(--color-main-background);\n\t\tz-index: -1;\n\t\tform,\n\t\tdiv {\n\t\t\tdisplay: inline-flex;\n\t\t\twidth: 100%;\n\t\t}\n\t\tinput {\n\t\t\tpadding: 5px;\n\t\t\tmargin-right: 0;\n\t\t\theight: 38px;\n\t\t\t&:hover,\n\t\t\t&:focus {\n\t\t\t\t/* overlapp borders */\n\t\t\t\tz-index: 1;\n\t\t\t}\n\t\t}\n\t\tinput[type='text'] {\n\t\t\twidth: 100%;\n\t\t\tmin-width: 0; /* firefox hack: override auto */\n\t\t\tborder-bottom-right-radius: 0;\n\t\t\tborder-top-right-radius: 0;\n\t\t}\n\t\tbutton,\n\t\tinput:not([type='text']) {\n\t\t\twidth: 36px;\n\t\t\theight: 38px;\n\t\t\tflex: 0 0 36px;\n\t\t\t&:not(:last-child) {\n\t\t\t\tborder-radius: 0 !important;\n\t\t\t}\n\t\t\t&:not(:first-child) {\n\t\t\t\tmargin-left: -1px;\n\t\t\t}\n\t\t\t&:last-child {\n\t\t\t\tborder-bottom-right-radius: var(--border-radius);\n\t\t\t\tborder-top-right-radius: var(--border-radius);\n\t\t\t\tborder-bottom-left-radius: 0;\n\t\t\t\tborder-top-left-radius: 0;\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * Deleted entries with undo button\n\t */\n\t.app-navigation-entry-deleted {\n\t\tdisplay: inline-flex;\n\t\tpadding-left: 44px;\n\t\ttransform: translateX(#{$navigation-width});\n\t\t.app-navigation-entry-deleted-description {\n\t\t\tposition: relative;\n\t\t\twhite-space: nowrap;\n\t\t\ttext-overflow: ellipsis;\n\t\t\toverflow: hidden;\n\t\t\tflex: 1 1 0px;\n\t\t\tline-height: 44px;\n\t\t}\n\t\t.app-navigation-entry-deleted-button {\n\t\t\tmargin: 0;\n\t\t\theight: 44px;\n\t\t\twidth: 44px;\n\t\t\tline-height: 44px;\n\t\t\t&:hover,\n\t\t\t&:focus {\n\t\t\t\topacity: 1;\n\t\t\t}\n\t\t}\n\t}\n\n\t/**\n\t * Common rules for animation of undo and edit entries\n\t */\n\t.app-navigation-entry-edit,\n\t.app-navigation-entry-deleted  {\n\t\twidth: calc(100% - 1px); /* Avoid border overlapping */\n\t\ttransition: transform 250ms ease-in-out,\n\t\t\t\t\topacity 250ms ease-in-out,\n\t\t\t\t\tz-index 250ms ease-in-out;\n\t\tposition: absolute;\n\t\tleft: 0;\n\t\tbackground-color: var(--color-main-background);\n\t\tbox-sizing: border-box;\n\t}\n\n\t/**\n\t * drag and drop\n\t */\n\t.drag-and-drop {\n\t\t-webkit-transition: padding-bottom 500ms ease 0s;\n\t\ttransition: padding-bottom 500ms ease 0s;\n\t\tpadding-bottom: 40px;\n\t}\n\n\t.error {\n\t\tcolor: var(--color-error);\n\t}\n\n\t.app-navigation-entry-utils ul,\n\t.app-navigation-entry-menu ul {\n\t\tlist-style-type: none;\n\t}\n}\n\n\n/* CONTENT  --------------------------------------------------------- */\n#content {\n\tbox-sizing: border-box;\n\tposition: relative;\n\tdisplay: flex;\n\t// padding is included in height\n\tpadding-top: var(--header-height);\n\tmin-height: 100%;\n}\n\n/* APP-CONTENT AND WRAPPER ------------------------------------------ */\n/* Part where the content will be loaded into */\n\n/**\n * !Important. We are defining the minimum requirement we want for flex\n * Just before the mobile breakpoint we have $breakpoint-mobile (1024px) - $navigation-width\n * -> 468px. In that case we want 200px for the list and 268px for the content\n */\n$min-content-width: $breakpoint-mobile - $navigation-width - $list-min-width;\n\n#app-content {\n\tz-index: 1000;\n\tbackground-color: var(--color-main-background);\n\tposition: relative;\n\tflex-basis: 100vw;\n\tmin-height: 100%;\n\t/* margin if navigation element is here */\n\t#app-navigation:not(.hidden) + & {\n\t\tmargin-left: $navigation-width;\n\t}\n\t/* no top border for first settings item */\n\t> .section:first-child {\n\t\tborder-top: none;\n\t}\n\n\t/* if app-content-list is present */\n\t#app-content-wrapper {\n\t\tdisplay: flex;\n\t\tposition: relative;\n\t\talign-items: stretch;\n\t\t/* make sure we have at least full height for loaders or such\n\t\t   no need for list/details since we have a flex stretch */\n\t\tmin-height: 100%;\n\n\t\t/* CONTENT DETAILS AFTER LIST*/\n\t\t.app-content-details {\n\t\t\t/* grow full width */\n\t\t\tflex: 1 1 $min-content-width;\n\t\t\t#app-navigation-toggle-back {\n\t\t\t\tdisplay: none;\n\t\t\t}\n\t\t}\n\t}\n}\n\n/* APP-SIDEBAR ------------------------------------------------------------ */\n/*\n\tSidebar: a sidebar to be used within #content\n\t#app-content will be shrinked properly\n*/\n#app-sidebar {\n\twidth: 27vw;\n\tmin-width: $sidebar-min-width;\n\tmax-width: $sidebar-max-width;\n\tdisplay: block;\n\tposition: 'sticky';\n\ttop: var(--header-height);\n\tright:0;\n\toverflow-y: auto;\n\toverflow-x: hidden;\n\tz-index: 1500;\n\theight: calc(100vh - var(--header-height));\n\tbackground: var(--color-main-background);\n\tborder-left: 1px solid var(--color-border);\n\tflex-shrink: 0;\n\t// no animations possible, use OC.Apps.showAppSidebar\n\t&.disappear {\n\t\tdisplay: none;\n\t}\n}\n\n\n/* APP-SETTINGS ------------------------------------------------------------ */\n/* settings area */\n#app-settings {\n\t// To the bottom w/ flex\n\tmargin-top: auto;\n\t&.open,\n\t&.opened {\n\t\t#app-settings-content {\n\t\t\tdisplay: block;\n\t\t}\n\t}\n}\n\n#app-settings-content {\n\tdisplay: none;\n\tpadding: 10px;\n\tbackground-color: var(--color-main-background);\n\t/* restrict height of settings and make scrollable */\n\tmax-height: 300px;\n\toverflow-y: auto;\n\tbox-sizing: border-box;\n\n\t/* display input fields at full width */\n\tinput[type='text'] {\n\t\twidth: 93%;\n\t}\n\n\t.info-text {\n\t\tpadding: 5px 0 7px 22px;\n\t\tcolor: var(--color-text-lighter);\n\t}\n\tinput {\n\t\t&[type='checkbox'],\n\t\t&[type='radio'] {\n\t\t\t&.radio,\n\t\t\t&.checkbox {\n\t\t\t\t+ label {\n\t\t\t\t\tdisplay: inline-block;\n\t\t\t\t\twidth: 100%;\n\t\t\t\t\tpadding: 5px 0;\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t}\n}\n\n#app-settings-header {\n\tbox-sizing: border-box;\n\tbackground-color: var(--color-main-background);\n}\n\n\n#app-settings-header .settings-button {\n\tdisplay: block;\n\theight: 44px;\n\twidth: 100%;\n\tpadding: 0;\n\tmargin: 0;\n\tbackground-color: var(--color-main-background);\n\t@include icon-color('settings-dark', 'actions', $color-black, 1, true);\n\tbackground-position: 14px center;\n\tbackground-repeat: no-repeat;\n\tbox-shadow: none;\n\tborder: 0;\n\tborder-radius: 0;\n\ttext-align: left;\n\tpadding-left: 44px;\n\tfont-weight: normal;\n\tfont-size: 100%;\n\topacity: 0.8;\n\n\t/* like app-navigation a */\n\tcolor: var(--color-main-text);\n\n\t&.opened {\n\t\tborder-top: solid 1px var(--color-border);\n\t\tbackground-color: var(--color-main-background);\n\t}\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: var(--color-background-hover);\n\t}\n}\n\n/* GENERAL SECTION ------------------------------------------------------------ */\n.section {\n\tdisplay: block;\n\tpadding: 30px;\n\tmargin-bottom: 24px;\n\t&.hidden {\n\t\tdisplay: none !important;\n\t}\n\t/* slight position correction of checkboxes and radio buttons */\n\tinput {\n\t\t&[type='checkbox'],\n\t\t&[type='radio'] {\n\t\t\tvertical-align: -2px;\n\t\t\tmargin-right: 4px;\n\t\t}\n\t}\n}\n.sub-section {\n\tposition: relative;\n\tmargin-top: 10px;\n\tmargin-left: 27px;\n\tmargin-bottom: 10px;\n}\n\n.appear {\n\topacity: 1;\n\t-webkit-transition: opacity 500ms ease 0s;\n\t-moz-transition: opacity 500ms ease 0s;\n\t-ms-transition: opacity 500ms ease 0s;\n\t-o-transition: opacity 500ms ease 0s;\n\ttransition: opacity 500ms ease 0s;\n\t&.transparent {\n\t\topacity: 0;\n\t}\n}\n\n/* TABS ------------------------------------------------------------ */\n.tabHeaders {\n\tdisplay: flex;\n\tmargin-bottom: 16px;\n\n\t.tabHeader {\n\t\tdisplay: flex;\n\t\tflex-direction: column;\n\t\tflex-grow: 1;\n\t\ttext-align: center;\n\t\twhite-space: nowrap;\n\t\toverflow: hidden;\n\t\ttext-overflow: ellipsis;\n\t\tcursor: pointer;\n\t\tcolor: var(--color-text-lighter);\n\t\tmargin-bottom: 1px;\n\t\tpadding: 5px;\n\n\t\t&.hidden {\n\t\t\tdisplay: none;\n\t\t}\n\n\t\t/* Use same amount as sidebar padding */\n\t\t&:first-child {\n\t\t\tpadding-left: 15px;\n\t\t}\n\t\t&:last-child {\n\t\t\tpadding-right: 15px;\n\t\t}\n\n\t\t.icon {\n\t\t\tdisplay: inline-block;\n\t\t\twidth: 100%;\n\t\t\theight: 16px;\n\t\t\tbackground-size: 16px;\n\t\t\tvertical-align: middle;\n\t\t\tmargin-top: -2px;\n\t\t\tmargin-right: 3px;\n\t\t\topacity: .7;\n\t\t\tcursor: pointer;\n\t\t}\n\n\t\ta {\n\t\t\tcolor: var(--color-text-lighter);\n\t\t\tmargin-bottom: 1px;\n\t\t\toverflow: hidden;\n\t\t\ttext-overflow: ellipsis;\n\t\t}\n\t\t&.selected {\n\t\t\tfont-weight: bold;\n\t\t}\n\t\t&.selected,\n\t\t&:hover,\n\t\t&:focus {\n\t\t\tmargin-bottom: 0px;\n\t\t\tcolor: var(--color-main-text);\n\t\t\tborder-bottom: 1px solid var(--color-text-lighter);\n\t\t}\n\t}\n}\n.tabsContainer {\n\tclear: left;\n\t.tab {\n\t\tpadding: 0 15px 15px;\n\t}\n}\n\n/* POPOVER MENU ------------------------------------------------------------ */\n$popoveritem-height: 44px;\n$popovericon-size: 16px;\n$outter-margin: ($popoveritem-height - $popovericon-size) / 2;\n\n.ie,\n.edge {\n\t.bubble, .bubble:after,\n\t.popovermenu, .popovermenu:after,\n\t#app-navigation .app-navigation-entry-menu,\n\t#app-navigation .app-navigation-entry-menu:after {\n\t\tborder: 1px solid var(--color-border);\n\t}\n}\n\n.bubble,\n.app-navigation-entry-menu,\n.popovermenu {\n\tposition: absolute;\n\tbackground-color: var(--color-main-background);\n\tcolor: var(--color-main-text);\n\tborder-radius: var(--border-radius);\n\tz-index: 110;\n\tmargin: 5px;\n\tmargin-top: -5px;\n\tright: 0;\n\tfilter: drop-shadow(0 1px 3px var(--color-box-shadow));\n\tdisplay: none;\n\twill-change: filter;\n\n\t&:after {\n\t\tbottom: 100%;\n\t\t// Required right-distance is half menu icon size + right padding\n\t\t// = 16px/2 + 14px = 22px\n\t\t// popover right margin is 5px, arrow width is 9px to center and border is 1px\n\t\t// 22px - 9px - 5px - 1px = 7px\n\t\tright: 7px;\n\t\t/* change this to adjust the arrow position */\n\t\tborder: solid transparent;\n\t\tcontent: ' ';\n\t\theight: 0;\n\t\twidth: 0;\n\t\tposition: absolute;\n\t\tpointer-events: none;\n\t\tborder-bottom-color: var(--color-main-background);\n\t\tborder-width: 9px;\n\t}\n\t/* Center the popover */\n\t&.menu-center {\n\t\ttransform: translateX(50%);\n\t\tright: 50%;\n\t\tmargin-right: 0;\n\t\t&:after {\n\t\t\tright: 50%;\n\t\t\ttransform: translateX(50%);\n\t\t}\n\t}\n\t/* Align the popover to the left */\n\t&.menu-left {\n\t\tright: auto;\n\t\tleft: 0;\n\t\tmargin-right: 0;\n\t\t&:after {\n\t\t\tleft: 6px;\n\t\t\tright: auto;\n\t\t}\n\t}\n\n\t&.open {\n\t\tdisplay: block;\n\t}\n\n\t&.contactsmenu-popover {\n\t\tmargin: 0;\n\t}\n\n\tul {\n\t\t/* Overwrite #app-navigation > ul ul */\n\t\tdisplay: flex !important;\n\t\tflex-direction: column;\n\t}\n\tli {\n\t\tdisplay: flex;\n\t\tflex: 0 0 auto;\n\n\t\t&.hidden {\n\t\t\tdisplay: none;\n\t\t}\n\n\t\t> button,\n\t\t> a,\n\t\t> .menuitem {\n\t\t\tcursor: pointer;\n\t\t\tline-height: $popoveritem-height;\n\t\t\tborder: 0;\n\t\t\tborder-radius: 0; // otherwise Safari will cut the border-radius area\n\t\t\tbackground-color: transparent;\n\t\t\tdisplay: flex;\n\t\t\talign-items: flex-start;\n\t\t\theight: auto;\n\t\t\tmargin: 0;\n\t\t\tfont-weight: normal;\n\t\t\tbox-shadow: none;\n\t\t\twidth: 100%;\n\t\t\tcolor: var(--color-main-text);\n\t\t\twhite-space: nowrap;\n\n\t\t\tspan[class^='icon-'],\n\t\t\tspan[class*=' icon-'],\n\t\t\t&[class^='icon-'],\n\t\t\t&[class*=' icon-'] {\n\t\t\t\tmin-width: 0; /* Overwrite icons*/\n\t\t\t\tmin-height: 0;\n\t\t\t\tbackground-position: #{($popoveritem-height - $popovericon-size) / 2} center;\n\t\t\t\tbackground-size: $popovericon-size;\n\t\t\t}\n\t\t\tspan[class^='icon-'],\n\t\t\tspan[class*=' icon-'] {\n\t\t\t\t/* Keep padding to define the width to\n\t\t\t\t assure correct position of a possible text */\n\t\t\t\tpadding: #{$popoveritem-height / 2} 0 #{$popoveritem-height / 2} $popoveritem-height;\n\t\t\t}\n\t\t\t// If no icons set, force left margin to align\n\t\t\t&:not([class^='icon-']):not([class*='icon-']) {\n\t\t\t\t> span,\n\t\t\t\t> input,\n\t\t\t\t> form {\n\t\t\t\t\t&:not([class^='icon-']):not([class*='icon-']):first-child {\n\t\t\t\t\t\tmargin-left: $popoveritem-height;\n\t\t\t\t\t}\n\t\t\t\t}\n\t\t\t}\n\t\t\t&[class^='icon-'],\n\t\t\t&[class*=' icon-'] {\n\t\t\t\tpadding: 0 #{($popoveritem-height - $popovericon-size) / 2} 0 $popoveritem-height !important;\n\t\t\t}\n\t\t\t&:hover,\n\t\t\t&:focus {\n\t\t\t\tbackground-color: var(--color-background-hover);\n\t\t\t}\n\t\t\t&.active {\n\t\t\t\tbackground-color: var(--color-primary-light);\n\t\t\t}\n\t\t\t/* prevent .action class to break the design */\n\t\t\t&.action {\n\t\t\t\tpadding: inherit !important;\n\t\t\t}\n\t\t\t> span {\n\t\t\t\tcursor: pointer;\n\t\t\t\twhite-space: nowrap;\n\t\t\t}\n\t\t\t> p {\n\t\t\t\twidth: 150px;\n\t\t\t\tline-height: 1.6em;\n\t\t\t\tpadding: 8px 0;\n\t\t\t\twhite-space: normal;\n\t\t\t}\n\t\t\t> select {\n\t\t\t\tmargin: 0;\n\t\t\t\tmargin-left: 6px;\n\t\t\t}\n\t\t\t/* Add padding if contains icon+text */\n\t\t\t&:not(:empty) {\n\t\t\t\tpadding-right: $outter-margin !important;\n\t\t\t}\n\t\t\t/* DEPRECATED! old img in popover fallback\n\t\t\t * TODO: to remove */\n\t\t\t> img {\n\t\t\t\twidth: $popovericon-size;\n\t\t\t\tpadding: #{($popoveritem-height - $popovericon-size) / 2};\n\t\t\t}\n\t\t\t/* checkbox/radio fixes */\n\t\t\t> input.radio + label,\n\t\t\t> input.checkbox + label {\n\t\t\t\tpadding: 0 !important;\n\t\t\t\twidth: 100%;\n\t\t\t}\n\t\t\t> input.checkbox + label::before {\n\t\t\t\tmargin: -2px 13px 0;\n\t\t\t}\n\t\t\t> input.radio + label::before {\n\t\t\t\tmargin: -2px 12px 0;\n\t\t\t}\n\t\t\t> input:not([type=radio]):not([type=checkbox]):not([type=image]) {\n\t\t\t\twidth: 150px;\n\t\t\t}\n\t\t\tform {\n\t\t\t\tdisplay: flex;\n\t\t\t\tflex: 1 1 auto;\n\t\t\t\t/* put a small space between text and form\n\t\t\t\t 